import { Component, OnInit } from '@angular/core';
import { SearchService } from './search.service';
import { Travel } from '../models/travel';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {
count1 = 1;
count2 = 0;
search_res: Travel;
count3 = 0;
isLoaded = false;
all: number;
str: string;
baby: string;
visibility: boolean = true;
  constructor(private searchService: SearchService) { }

  ngOnInit() {
    this.value();
  }
 
  toggle(){
    this.visibility=!this.visibility;
}
search(){
  this.searchService.getOptions().subscribe((res) => {
    this.search_res = res;
    this.isLoaded = true;
    console.log(this.search_res.hotel);
  });
}
  plus1(){
    this.count1++;
    console.log(this.count1);
    this.value();
  }
  plus2(){
    this.count2++;
    console.log(this.count2);
    this.value();
  }
  plus3(){
    this.count3++;
    console.log(this.count3);
    this.value();
  }

  minus1(){
    if(this.count1 == 0){
      this.count1 = 0;
    } else{
      this.count1--;
      console.log(this.count1);
    }
    this.value();
  }
  minus2(){
    if(this.count2 == 0){
      this.count2 = 0;
    } else {
      this.count2--;
      console.log(this.count2);
    }
    this.value();
  }
  minus3(){
    if(this.count3 == 0){
      this.count3 = 0;
    } else{
      this.count3--;
      console.log(this.count3);
    }
    this.value();
  }
  value(){
    this.all = this.count1 + this.count2;
    if(this.all == 1){
      this.str = this.all.toString() + ' гость';
    } else if(this.all > 1 && this.all < 5){
      this.str = this.all.toString() + ' гостя';
    } else if( this.all > 4 && this.all<21){
      this.str = this.all.toString() + ' гостей';
    }

    if(this.count3 == 1){
      this.baby = this.count3.toString() + ' младенец';
    } else if(this.count3 > 1 && this.count3 < 5){
      this.baby = this.count3.toString() + ' младенца';
    } else if( this.count3 > 4 && this.count3<21){
      this.baby = this.count3.toString() + ' младенцев';
    }
  }
}
